import api from "../api";
export const ACTION_TYPES = {
    CREATESTORES : 'CREATESTORES',
    UPDATESTORES: 'UPDATESTORES',
    DELETESTORES: 'DELETESTORES',
    FETCH_ALLSTORES: 'FETCH_ALLSTORES'
}

