//short version of fetch
export  const fetchAllStore = () =>dispatch =>{
    //get api req.
    api.stores().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLSTORES,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createStore =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.stores().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATESTORES,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateStore =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.stores().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATESTORES,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteStore =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.stores().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETESTORES,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}