import api from "../api";
export const ACTION_TYPES = {
    CREATEEXPENSES : 'CREATEEXPENSES',
    UPDATEEXPENSES: 'UPDATEEXPENSES',
    DELETEEXPENSES: 'DELETEEXPENSES',
    FETCH_ALLEXPENSES: 'FETCH_ALLEXPENSES'
}

