//short version of fetch
export  const fetchAllExpenses = () =>dispatch =>{
    //get api req.
    api.expenses().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLEXPENSES,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createExpenses =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.expenses().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATEEXPENSES,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateExpenses=(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.expenses().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES. UPDATEEXPENSES,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteExpenses =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.expenses().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETEEXPENSES,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}