import api from "../api";
export const ACTION_TYPES = {
    CREATECATEGORIES : 'CREATECATEGORIES',
    UPDATECATEGORIES: 'UPDATECATEGORIES',
    DELETECATEGORIES: 'DELETECATEGORIES',
    FETCH_ALLCATEGORIES: 'FETCH_ALLCATEGORIES'
}

