//short version of fetch
export  const fetchAllCategory = () =>dispatch =>{
    //get api req.
    api.category().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLCATEGORIES,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createCategory =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.category().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATECATEGORIES,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateCategory =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.category().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATECATEGORIES,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteCategory =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.category().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETECATEGORIES,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}