import api from "../api";
export const ACTION_TYPES = {
    //CREATEUSERLEVEL : 'CREATEUSERLEVEL',
    UPDATEACCOUNT: 'UPDATEACCOUNT',
    FETCHUSERACCOUNT: 'FETCHUSERACCOUNT',
    FETCH_ALLACCOUNTS: 'FETCH_ALLACCOUNTS'
}


//short version of fetch
export  const fetchAllAccounts = () =>dispatch =>{
    //get api req.
    api.accounts().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLACCOUNTS,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

