export const updateAccount =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.accounts().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATEACCOUNT,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

