import { ACTION_TYPES } from "../../actions/StockIn/stockIn";
const initialState = {
    list: []
}

export const stockIns = (state = initialState, action) => {
    switch (action.type) {
        case ACTION_TYPES.FETCH_ALLSTOCKINS:
            return {
                ...state,
                list: [...action.payload]
            }
        case ACTION_TYPES.CREATESTOCKINS:
            return {
                ...state,
                list: [...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATESTOCKINS:
            return {
                ...state,
                list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
            }

        case ACTION_TYPES.FETCHBYID:
            return {
                ...state,
                list: state.list.filter(x => x.id != action.payload)
            }

        case ACTION_TYPES.FETCHBYDATEIN:
            return {
                ...state,
                list: state.list.filter(x => x.id != action.payload)
            }

        case ACTION_TYPES.FETCHBYDATEINSTOREID:
            return {
                ...state,
                list: state.list.filter(x => x.id != action.payload)
            }
        case ACTION_TYPES.FETCH_ALLSTOCKINS:
            return {
                ...state,
                list: state.list.filter(x => x.id != action.payload)
            }

        default:
            return state
    }
}