import { ACTION_TYPES } from "../../actions/Trucks_Cadetes/Trucks_Cadetes";
const initialState = {
    list: []
}

export const trucks_cadetes = (state = initialState, action) => {
    switch (action.type) {
        case ACTION_TYPES.FETCH_ALLCADETETRANS:
            return {
                ...state,
                list: [...action.payload]
            }

        case ACTION_TYPES.FETCH_ALLCADETETRANSBYTRANSCODE:
            return {
                ...state,
                list: state.list.filter(x => x.transCode != action.payload)
            }

        case ACTION_TYPES.FETCH_ALLCADETETRANSBYDATEIN:
            return {
                ...state,
                list: state.list.filter(x => x.dateIn != action.payload)
            }

        case ACTION_TYPES.FETCH_ALLCADETETRANSBYDATEINNUSERID:
            return {
                ...state,
                list: state.list.filter(x => x.dateIn != action.payload && x.userId != action.payload)
            }

        case ACTION_TYPES.FETCH_ALLCADETETRANSBYUSERID:
            return {
                ...state,
                list: state.list.filter(x => x.userId != action.payload)
            }



        // case ACTION_TYPES.UPDATEPRODUCTS:
        //     return {
        //         ...state,
        //         list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
        //     }

        case ACTION_TYPES.UPDATECADETETRANSACTIONS:
            return {
                ...state,
                list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
            }

        case ACTION_TYPES.TOPUPTRANS:
            return {
                ...state,
                list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
            }

        case ACTION_TYPES.DELETEALLCADETETRANS:
            return {
                ...state,
                list: state.list.filter(x => x.transCode != action.payload)
            }
        default:
            return state
    }
}