import api from "../api";
export const ACTION_TYPES = {
    CREATEUSERLEVEL : 'CREATEUSERLEVEL',
    UPDATEUSERLEVEL: 'UPDATEUSERLEVEL',
    DELETEUSERLEVEL: 'DELETEUSERLEVEL',
    FETCH_ALLUSERLEVEL: 'FETCH_ALLUSERLEVEL'
}

//long version of fetch all
