//short version of fetch
export  const fetchAllUserLevel = () =>dispatch =>{
    //get api req.
    api.userLevels().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLUSERLEVEL,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createUserLevel =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.userLevels().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATEUSERLEVEL,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateUserLevel =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.userLevels().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATEUSERLEVEL,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteUserLevel =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.userLevels().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETEUSERLEVEL,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}