import api from "../api";
export const ACTION_TYPES = {
    CREATECREDITPAY : 'CREATECREDITPAY',
    UPDATECREDITPAY: 'UPDATECREDITPAY',
    DELETECREDITPAY: 'DELETECREDITPAY',
    FETCH_ALLCREDITPAY: 'FETCH_ALLCREDITPAY'
}

