//short version of fetch
export  const fetchAllCreditPay = () =>dispatch =>{
    //get api req.
    api.creditPay().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLCREDITPAY,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createCreditPay =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.creditPay().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATECREDITPAY,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateCreditPay=(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.creditPay().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES. UPDATECREDITPAY,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteCreditPay =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.creditPay().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETECREDITPAY,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}