import api from "../api";
export const ACTION_TYPES = {
    CREATETRANSACTION : 'CREATETRANSACTION',
    // UPDATEACCOUNT: 'UPDATEACCOUNT',
    FETCHTRANSACTIONSBYACCNO: 'FETCHTRANSACTIONSBYACCNO',
    FETCH_ALLTRANSACTIONS: 'FETCH_ALLTRANSACTIONS'
}


//short version of fetch
export  const fetchAllTransactions = () =>dispatch =>{
    //get api req.
    api.transactions().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLTRANSACTIONS,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createTransaction =(data, onSuccess) => dispatch =>{
    
     api.transactions().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATETRANSACTION,
             payload: res.data

         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export  const fetchTransactionsByAccNo = (accNo) =>dispatch =>{
    //get api req.
    console.log(accNo);
    api.transactions().fetchByAccNo(accNo)
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCHTRANSACTIONSBYACCNO,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}
