import { ACTION_TYPES } from "../../actions/Notifications/Notifications";
const initialState = {
    list: []
}

export const notifications = (state = initialState, action) => {
    switch (action.type) {
        case ACTION_TYPES.FETCH_ALLNOTIFICATIONS:
            return {
                ...state,
                list: [...action.payload]
            }

        case ACTION_TYPES.FETCH_ALLREADNOTIFICATIONS:
            return {
                ...state,
                list: [...action.payload]
            }

        case ACTION_TYPES.FETCH_ALLUNREADNOTIFICATIONS:
            return {
                ...state,
                list: [...action.payload]
            }
        case ACTION_TYPES.CREATENOTIFICATION:
            return {
                ...state,
                list: [...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATENOTIFICATION:
            return {
                ...state,
                list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
            }

        case ACTION_TYPES.DELETENOTIFICATION:
            return {
                ...state,
                list: state.list.filter(x => x.id != action.payload)
            }
        default:
            return state
    }
}