import React, { useState, useEffect } from 'react'
import { Link, useHistory, Redirect } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react';
import { useForm } from "../../Components/Forms/useForm";
import {  TextField } from "@material-ui/core";
import { RepeatOneSharp } from '@material-ui/icons';
import Register from '../register/Register';

const initialFieldValues = {

  email: '',
  password: '',


}


const Login = ({ ...props }) => {
  let history = useHistory();
  const TheLayout = React.lazy(() => import('../../../containers/TheLayout'));
  // const [email, setEmail] = useState('');
  // const [password, setPassword] = useState('');

  useEffect(() => {
    

    // logout();
    
   
  }, []);
  const validate = (fieldValues = values) => {
    let temp = { ...errors }
    
    if ('email' in fieldValues) {
        if (temp.email != " ") {
            temp.email = (/^&|.+@.+..+/).test(fieldValues.email) ? "" : "Email is not valid"
        }
    }
    
    if ('password' in fieldValues) {
        temp.password = fieldValues.password ? "" : "This field is required"
    }
   




    setErrors({
        ...temp
    })
    if (fieldValues == values) {
        return Object.values(temp).every(x => x == "")
    }


}

  const [
    values,
    setValues,
    errors,
    setErrors,
    handleInputChange,
    resetForm
] = useForm(initialFieldValues, validate,props.setCurrentId);

  const submit = async (e: SyntheticEvent) => {
    e.preventDefault();
   
    try{
       // if (validate()){
        // logout();
        const response = await fetch('http://risokaslick-001-site1.ktempurl.com/api/Auth/login', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          credentials: 'include',
          body: JSON.stringify({
            email:values.email,
            password:values.password
          })
        });

        
        
        if(response.ok === true){
          window.accessToken = true;
         props.setAccessToken(true);
        
         
         
          // console.log(window.accessToken);
         }
      
        

    }catch(err ){
      console.log(err);
    }
   
  }

  const logout = async () => {
    try{
     const response = await fetch('http://risokaslick-001-site1.ktempurl.com/api/Auth/logout/', {
       method: 'POST',
       headers: { 'Content-Type': 'application/json' },
       credentials: 'include',
 
     })
    //  window.accessToken = false;
    // console.log("Logged out");
 
    }catch{
 
    }
   }

   const handleButtonClick = () => {
    window.location.href = '';
  };

  const showRegister = async () =>{
    console.log("register clicked");
    props.setSignup(true);
  }
  return (
    <div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="8">
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <form onSubmit={submit}>
                    <h1>Login</h1>
                    <p className="text-muted">Sign In to your account</p>
                    <CInputGroup className="mb-3"> 
                      <CInputGroupPrepend>
                        <CInputGroupText >
                          <CIcon name="cil-user" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput  type="text" placeholder="Email" autoComplete="email" name="email" value={values.email} variant="outlined" onChange={handleInputChange} 
                      {... (errors.email && { error: true, helperText: errors.email })}
                      />
                      {/* <TextField className="form-ctr" name="email" label="Email" value={values.email}
                                    onChange={handleInputChange}
                                    variant="outlined"


                                    {... (errors.sName && { error: true, helperText: errors.sName })}
                                /> */}
                    </CInputGroup>
                    <CInputGroup className="mb-4">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-lock-locked" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput type="password" placeholder="Password" autoComplete="current-password" name="password" value={values.password}  onChange={handleInputChange} 
                      {... (errors.password && { error: true, helperText: errors.password })}
                      />
                    </CInputGroup>
                    <CRow>
                      <CCol xs="6">

                        {/* <Link to="/home"> */}
                        <button type="submit" className="btn btn-primary"
                        // onClick={()=>{props.setAccessToken(true)}}
                        >Login</button>
                        {/* </Link> */}
                      </CCol>
                      <CCol xs="6" className="text-right">
                        <CButton color="link" className="px-0" onClick={handleButtonClick}>SignUp</CButton>
                      </CCol>
                      <CCol xs="6" className="text-right">
                        <CButton color="link" className="px-0">Forgot password?</CButton>
                      </CCol>
                    </CRow>
                  </form>
                </CCardBody>
              </CCard>
              <CCard className="text-white bg-primary py-5 d-md-down-none" style={{ width: '44%' }}>
                <CCardBody className="text-center">
                  <div>
                    <h2>Sign up</h2>
                    <p>Click Register below and begin you investment journey!</p>
                    {/* <Link to="/register"> */}
                      {/* <CButton color="primary" className="mt-3" active tabIndex={-1} onClick={showRegister}>Register Now!</CButton> */}
                    {/* </Link> */}
                  </div>
                </CCardBody>
              </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default Login
