import {ACTION_TYPES} from "../../actions/Category/categories";
const initialState = {
    list:[]
}

export const category = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLCATEGORIES:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATECATEGORIES:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATECATEGORIES:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETECATEGORIES:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}