import {ACTION_TYPES} from "../../actions/Accounts/Accounts";
const initialState = {
    list:[]
}

export const accounts  = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLACCOUNTS:
            return{
                ...state,
                list:[...action.payload]
            }
        // case ACTION_TYPES.CREATEUSERLEVEL:
        //     return {
        //         ...state,
        //         list:[...state.list, action.payload]
        //     }

        
        case ACTION_TYPES.UPDATEACCOUNT:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            // case ACTION_TYPES.DELETEUSERLEVEL:
            // return {
            //     ...state,
            //     list: state.list.filter(x => x.id!= action.payload)
            // }
        default:
            return state
    }
}