import {combineReducers } from "redux";
import {addStaff} from "./Staff/addStaff";
import {stores} from "./Stores/stores";
import {userLevel} from "./UserLevel/UserLevel";
import {category} from "./Categories/categories";
import {products} from "./Products/products";
import {suppliers} from "./Suppliers/suppliers";
import {customers} from "./Customers/customers";
import {stockIns} from "./stockIn/stockIn";
import {storeProducts} from "./StoreProducts/storeProducts";
import {companyData} from "./CompanyData/companyData";
import {cashSale} from "./CashSale/cashSale";
import {creditSale} from "./CreditSale/creditSale";
import {trucks_cadetes} from "./Trucks_Cadetes/trucks_cadetes";
import {vehicles} from "./Vehicle/vehicle";
import {trucks} from "./Trucks_Cadetes/trucks";
import {expenseTypes} from "./ExpenseTypes/expenseTypes";
import {expenses} from "./Expenses/expenses";
import {cash} from "./CashSale/cash";
import {credit} from "./CreditSale/credit";
import {deposit} from "./Deposit/deposit";
import {tax} from "./Tax/tax";
import {creditPay} from "./CreditPay/creditPay";
import {notifications} from "./Notifications/notifications";
import {accounts} from "./Accounts/Accounts";
import { transactions } from "./Transactions/Transactions";
const initialState = {
    sidebarShow: 'responsive'
  }
  
  const changeState = (state = initialState, { type, ...rest }) => {
    switch (type) {
      case 'set':
        return {...state, ...rest }
      default:
        return state
    }
  }
const rootReducer = combineReducers({
    nav: changeState,
    addStaff,
    stores,
    userLevel,
    category,
    products,
    suppliers,
    customers,
    stockIns,
    storeProducts,
    companyData,
    cashSale,
    creditSale,
    trucks_cadetes,
    vehicles,
    trucks,
    expenseTypes,
    expenses,
    cash,
    credit,
    deposit,
    tax,
    creditPay,
    notifications,
    accounts,
    transactions
});

export default rootReducer; 