import {ACTION_TYPES} from "../../actions/Vehicles/vehicles";
const initialState = {
    list:[]
}

export const vehicles = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLVEHICLES:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATEVEHICLES:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATEVEHICLES:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETEVEHICLES:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}