import React from 'react';
import './dcard.css'; // Optional CSS for styling



const CreditCard = ({ accountNumber, accountName, accbalance, invLimit, dateCreated }) => {

    
    
    return (
        <div className="credit-card1">
        {/* Optional chip for a realistic look */}
        <div className="card-chip"></div>
        
        <div className="card-header1">
          <h2>{accountName}</h2>
        </div>
        
        <div className="card-body1">
          <div className="account-number1">
            <h3><span>AccNo:</span> {accountNumber.replace(/(.{4})/g, "$1 ")} {/* Format for readability */}</h3>
          </div>
          <div className="account-name1">
            <h3><span>Balance: </span>{accbalance.toFixed(2)}</h3>
          </div>
          <div className="account-name1">
            <h3><span>Investment Limit: </span>{invLimit.toFixed(2)}</h3>
          </div>
        </div>
  
        <div className="account-name1">
          <h5><span>Date Created: </span>{new Date(dateCreated).toLocaleDateString()}</h5>
        </div>
      </div>
    );
};

export default CreditCard;
