import { ACTION_TYPES } from "../../actions/CreditSale/creditSale";
const initialState = {
    list: []
}

export const credit = (state = initialState, action) => {
    switch (action.type) {
       
        case ACTION_TYPES.FETCH_ALLCREDITSALETRANSDETAILS:
            return {
                ...state,
                list: [...action.payload]
            }

        case ACTION_TYPES.FETCH_ALLCREDITSALETRANSDETAILSBYTRANSCODE:
            return {
                ...state,
                list: state.list.filter(x => x.transCode != action.payload)
            }

        
        case ACTION_TYPES.CREATECREDITSALETRANSDETAILS:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        // case ACTION_TYPES.UPDATEPRODUCTS:
        //     return {
        //         ...state,
        //         list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
        //     }

       
        default:
            return state
    }
}