import { ACTION_TYPES } from "../../actions/Trucks_Cadetes/Trucks_Cadetes";
const initialState = {
    list: []
}

export const trucks = (state = initialState, action) => {
    switch (action.type) {
        case ACTION_TYPES.FETCH_ALLCADETETRANSDETAILS:
            return {
                ...state,
                list: [...action.payload]
            }

        case ACTION_TYPES.FETCH_ALLCADETETRANSDETAILSBYTRANSCODE:
            return {
                ...state,
                list: state.list.filter(x => x.id != action.payload)
            }

        case ACTION_TYPES.CREATECADETETRANS:
            return {
                ...state,
                list: [...state.list, action.payload]
            }

        case ACTION_TYPES.CREATECADETETRANSDETAILS:
            return {
                ...state,
                list: [...state.list, action.payload]
            }

        // case ACTION_TYPES.UPDATEPRODUCTS:
        //     return {
        //         ...state,
        //         list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
        //     }

        case ACTION_TYPES.UPDATECADETETRANSACTIONSDETAILS:
            return {
                ...state,
                list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
            }

        case ACTION_TYPES.TOPUPTRANSDETAIL:
            return {
                ...state,
                list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
            }

        case ACTION_TYPES.DELETEALLCADETETRANS:
            return {
                ...state,
                list: state.list.filter(x => x.transCode != action.payload)
            }
        default:
            return state
    }
}