import api from "../api";
export const ACTION_TYPES = {
    CREATESTOREPRODUCTS : 'CREATESTOREPRODUCTS',
    UPDATESTOREPRODUCTS: 'UPDATESTOREPRODUCTS',
    DELETESTOREPRODUCTS: 'DELETESTOREPRODUCTS',
    FETCH_ALLSTOREPRODUCTS: 'FETCH_ALLSTOREPRODUCTS',
    FETCHBYSTOREID:'FETCHBYSTOREID',
    FETCHBYSTOREIDNPRODUCTID:'FETCHBYSTOREIDNPRODUCTID',
    //FETCHBYDATEINSTOREID:'FETCHBYDATEINSTOREID',

}

