//short version of fetch
export  const fetchAllStoreProducts = () =>dispatch =>{
    //get api req.
    api.storeProducts().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLSTOREPRODUCTS,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createStoreProduct =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.storeProducts().create(data)
     .then(response =>{
         dispatch({
             type:ACTION_TYPES.CREATESTOREPRODUCTS,
             payload: response.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateStoreProducts =(id,storeId,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.storeProducts().update(id,storeId,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATESTOREPRODUCTS,
             payload: {...id, ...storeId, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteStoreProduct =(id,storeId, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.storeProducts().delete(id,storeId)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETESTOREPRODUCTS,
             payload: {...id, ...storeId}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const fetchByStoreId =(id,storeId, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.storeProducts().fetchByStoreId(id,storeId)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.FETCHBYSTOREID,
             payload: {...id, ...storeId}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const fetchByStoreIdNProductId =(storeId,productId, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.storeProducts().fetchByStoreIdNProdId(storeId,productId)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.FETCHBYSTOREIDNPRODUCTID,
             payload: {storeId, ...productId}
         })
         onSuccess()
     })  
     .catch(err => console.log(err))
}

