import api from "../api";
export const ACTION_TYPES = {
    CREATEDEPOSIT : 'CREATEDEPOSIT',
    UPDATEDEPOSIT: 'UPDATEDEPOSIT',
    DELETEDEPOSIT: 'DELETEDEPOSIT',
    FETCH_ALLDEPOSIT: 'FETCH_ALLDEPOSIT'
}

