//short version of fetch
export  const fetchAllDeposit = () =>dispatch =>{
    //get api req.
    api.deposit().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLDEPOSIT,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createDeposit =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.deposit().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATEDEPOSIT,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateDeposit=(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.deposit().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES. UPDATEDEPOSIT,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteDeposit =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.deposit().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETEDEPOSIT,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}