import {ACTION_TYPES} from "../../actions/Deposit/deposit";
const initialState = {
    list:[]
}

export const deposit = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLDEPOSIT:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATEDEPOSIT:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATEDEPOSIT:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETEDEPOSIT:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}