import React from 'react'
import {
  TheContent,
  TheSidebar,
  TheFooter,
  TheHeader
} from './index'

const TheLayout = ({...props}) => {
  const { uName, level } = props;
  return (
    <div className="c-app c-default-layout">
      <TheSidebar/>
      <div className="c-wrapper">
        <TheHeader accessToken ={props.accessToken} setAccessToken ={props.setAccessToken}/>
        <div className="c-body">
        
          <TheContent uName={uName} level={level} />
        </div>
        <TheFooter />
      </div>
    </div>
  )
}

export default TheLayout
