import api from "../api";
export const ACTION_TYPES = {
    CREATEEXPENSETYPE : 'CREATEEXPENSETYPE',
    UPDATEEXPENSETYPE: 'UPDATEEXPENSETYPE',
    DELETEEXPENSETYPE: 'DELETEEXPENSETYPE',
    FETCH_ALLEXPENSETYPE: 'FETCH_ALLEXPENSETYPE'
}

