//short version of fetch
export  const fetchAllExpenseTypes = () =>dispatch =>{
    //get api req.
    api.expenseTypes().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLEXPENSETYPE,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createExpenseType =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.expenseTypes().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATEEXPENSETYPE,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateExpenseType =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.expenseTypes().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES. UPDATEEXPENSETYPE,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteExpenseType =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.expenseTypes().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETEEXPENSETYPE,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}