import {ACTION_TYPES} from "../../actions/CreditPayments/creditPay";
const initialState = {
    list:[]
}

export const creditPay = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLCREDITPAY:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATECREDITPAY:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATECREDITPAY:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETECREDITPAY:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}