import React, { Suspense, useEffect } from 'react'
import {
  Redirect,
  Route,
  Switch
} from 'react-router-dom'
import { CContainer, CFade } from '@coreui/react'

// routes config
import routes from '../routes'
import Dashboard from 'src/views/dashboard/Dashboard1/Dashboard'
import Dashboard2 from 'src/views/dashboard/Dashboard2/Dashboard2'
const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

const TheContent = (props) => {
  const { uName, level } = props;
 
  useEffect(() => {

  }, [level])
  return (
    <main className="c-main">
      <CContainer fluid>
        <Suspense fallback={loading}>
          <Switch>
            {routes.map((route, idx) => {
              return route.component && (
                <Route
                  key={idx}
                  path={route.path}
                  exact={route.exact}
                  name={route.name}
                  render={props => (
                    <CFade>
                      <route.component uName={uName} level={level} />
                    </CFade>
                  )} />
              )
            })}

            {/* <Redirect
              from="/"
              to={{
                pathname: "/dashboard",
                state: { uName, level }
              }}
            /> */}
            {level === "Admin" ? (
              // <Dashboard/>
              <Redirect from="/" to="/dashboard" {...props.uName} />
            ): <Dashboard2/>}

          </Switch>
        </Suspense>
      </CContainer>
    </main>
  )
}

export default React.memo(TheContent)
