import {ACTION_TYPES} from "../../actions/Suppliers/suppliers";
const initialState = {
    list:[]
}

export const suppliers = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLSUPPLIERS:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATESUPPLIERS:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATESUPPLIERS:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETESUPPLIERS:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}