import {ACTION_TYPES} from "../../actions/Customers/customers";
const initialState = {
    list:[]
}

export const customers = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLCUSTOMERS:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATECUSTOMERS:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATECUSTOMERS:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETECUSTOMERS:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}