import api from "../api";
export const ACTION_TYPES = {
    CREATE : 'CREATE',
    UPDATE: 'UPDATE',
    DELETE: 'DELETE',
    FETCH_ALL: 'FETCH_ALL',
    FETCH_ALL_INVESTORS: 'FETCH_ALL_INVESTORS',
    FETCH_ALL_ADMIN: 'FETCH_ALL_ADMIN'
}

