//short version of fetch
export  const fetchAll = () =>dispatch =>{
    //get api req.
    api.addStaff().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALL,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export  const fetchAllInvestors = () =>dispatch =>{
    //get api req.
    api.addStaff().fetchAllInvestors()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALL_INVESTORS,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

