import api from "../api";
export const ACTION_TYPES = {
    CREATECUSTOMERS : 'CREATECUSTOMERS',
    UPDATECUSTOMERS: 'UPDATECUSTOMERS',
    DELETECUSTOMERS: 'DELETECUSTOMERS',
    FETCH_ALLCUSTOMERS: 'FETCH_ALLCUSTOMERS'
}

