//short version of fetch
export  const fetchAllCustomer = () =>dispatch =>{
    //get api req.
    api.customers().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLCUSTOMERS,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createCustomer =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.customers().create(data)
     .then(res =>{
         dispatch({
        
             type:ACTION_TYPES.CREATECUSTOMERS,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateCustomer =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.customers().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATECUSTOMERS,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteCustomer =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.customers().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETECUSTOMERS,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}