import {ACTION_TYPES} from "../../actions/CompanyData/companyData";
const initialState = {
    list:[]
}

export const companyData  = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLCOMPANYDATA:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATECOMPANYDATA:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATECOMPANYDATA:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETECOMPANYDATA:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}