//import Controls from "./Controls";
import NotListedLocation from '@material-ui/icons/NotListedLocation';
import "../style.css";
import * as companyDataActions from "../../../../actions/CompanyData/companyData";
import ScriptTag from 'react-script-tag';
import ReactToPrint from 'react-to-print';
import { useReactToPrint } from 'react-to-print';
import Controls from "../../../Components/controls/Controls";
import * as TActions from "../../../../actions/Tax/tax";
const styles = theme => ({
    root: {
        "& .MuiTableCell-head": {
            fontSize: "0.95rem"
        }
    },
    paper: {
        margin: theme.spacing(-8.9),
        padding: theme.spacing(6)
    },

    grid: {
        paddingLeft: theme.spacing(2)
    }
})



const PrintReciept = ({ classes, ...props }) => {
    const componentRef = useRef();
    // const [taxId, setTaxId] = useState("");
    let taxRate = "";
    let totalVat = 0;
    useEffect(() => {
        // props.fetchAllCategory()
        // props.fetchAllStores()
        props.fetchAllTaxes();
    }, [])


    // const getTax = (id,price) => {
    //     const tax = { ...props.taxList.find(x => x.taxId === id) };
    //     // setTaxId(tax.taxId);
    //     taxRate = tax.rate / 100;
    //     let vat = price * taxRate;
    //     totalVat = totalVat + vat;
    //     console.log(tax.rate);


    // }
    var totalTax  = (props.recordList.reduce((totalTax, currentItem) => totalTax = totalTax + currentItem.tax, 0));
    return (
        <>
            <div >
                <div
                    className="ticket"
                    ref={componentRef}
                >
                    {/* <img src="./logo.png" alt="Logo" /> */}
                    <p className="centered"> {props.companyData[0].name}
                        <br />Phone: {props.companyData[0].phone}
                        <br />Address:{props.companyData[0].address}
                        <br />Reciept No: {props.trans.transCode}
                        <br /> PIN: {props.companyData[0].krapin}
                        <br />{props.customerData != undefined ? "Customer Name:" + props.customerData[0].name : ""}
                        <br /> {props.cadeteName != undefined ? "Cadete Name: " + props.cadeteName + " | " + "Truck/Van: " + props.vehicle : ""}

                    </p>
                    <table>
                        <thead>
                            <tr>
                                <th className="quantity">Q.</th>
                                <th className="description">Description</th>
                                <th className="price">KSh</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                props.recordList.map((record, index) => {

                                    return (
                                        <>
                                        <tr>
                                            <td className="quantity">{record.quantity}</td>
                                            <td className="description">{record.name}</td>
                                            <td className="price">
                                                {record.totalPrice}
                                            </td>
                                        </tr>
                                        <tr>
                                                {/* {getTax(record.taxId,record.totalPrice)} */}
                                                <td></td>
                                                <td>VAT</td>

                                                <td>
                                                    {
                                                        parseFloat(record.tax).toFixed(2)
                                                    }
                                                </td>
                                            </tr>
                                        </>
                                        

                                    )
                                })
                            }
                            <br />
                            <tr>
                                <td className="Total"></td>
                                {/* <td className="description">Deposit</td>
                                <td className="price">{props.trans.gDeposit}</td> */}
                            </tr>

                            
                            <tr>
                                <td className="Total">{props.trans.quantity}</td>
                                <td className="description">Total</td>
                                <td className="price">{props.trans.gTotal}</td>
                            </tr>
                            <tr>
                                <td></td>
                                <td className="description">Total VAT</td>
                                <td className="price">{parseFloat(totalTax).toFixed(2)}</td>
                            </tr>


                            {/* <tr>
                                <td className="quantity">1.00</td>
                                <td className="description">ARDUINO UNO R3</td>
                                <td className="price">$25.00</td>
                            </tr>
                            <tr>
                                <td className="quantity">2.00</td>
                                <td className="description">JAVASCRIPT BOOK</td>
                                <td className="price">$10.00</td>
                            </tr>
                            <tr>
                                <td className="quantity">1.00</td>
                                <td className="description">STICKER PACK</td>
                                <td className="price">$10.00</td>
                            </tr>
                            <tr>
                                <td className="quantity" />
                                <td className="description">TOTAL</td>
                                <td className="price">$55.00</td>
                            </tr> */}
                        </tbody>
                    </table>
                    <p className="centered">Thanks for your purchase!
                        <br /></p>
                </div>

                <ScriptTag isHydrating={true} type="text/javascript" src="./print.js" />
                <ReactToPrint
                    trigger={() => <Controls.Button
                        text="Print"
                        variant="outlined"
                        //   startIcon={<AddIcon />}
                        className={classes.newButton}
                        onClick={() => { props.setOpenPopup(false); }}
                    />
                    }
                    content={() => componentRef.current}

                />
            </div>

        </>


    );
}

//export default staff; 

const mapStateToProps = state => ({
    // categoryList: state.category.list
    taxList: state.tax.list

})

const mapActionToProps = {
    // fetchAllCategory: Cactions.fetchAllCategory,
    // deleteCategory: Cactions.DeleteCategory,
    fetchAllTaxes: TActions.fetchAllTaxes

}

export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(PrintReciept));

