import api from "../api";
export const ACTION_TYPES = {
    CREATEVEHICLES : 'CREATEVEHICLES',
    UPDATEVEHICLES: 'UPDATEVEHICLES',
    DELETEVEHICLES: 'DELETEVEHICLES',
    FETCH_ALLVEHICLES: 'FETCH_ALLVEHICLES'
}

