//short version of fetch
export  const fetchAllVehicles = () =>dispatch =>{
    //get api req.
    api.vehicle().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLVEHICLES,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createVehicle =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.vehicle().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATEVEHICLES,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateVehicle =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.vehicle().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATEVEHICLES,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteVehicle =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.vehicle().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETEVEHICLES,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}