
import api from "../api";
export const ACTION_TYPES = {
    CREATECOMPANYDATA : 'CREATECOMPANYDATA',
    UPDATECOMPANYDATA: 'UPDATECOMPANYDATA',
    DELETECOMPANYDATA: 'DELETECOMPANYDATA',
    FETCH_ALLCOMPANYDATA: 'FETCH_ALLCOMPANYDATA'
}

