//short version of fetch
export  const fetchAllCompanyData = () =>dispatch =>{
    //get api req.
    api.companyData().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLCOMPANYDATA,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createCompanyData =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.companyData().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATECOMPANYDATA,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateCompanyData =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.companyData().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATECOMPANYDATA,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteCompanyData =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.companyData().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETECOMPANYDATA,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}