import {ACTION_TYPES} from "../../actions/Tax/tax";
const initialState = {
    list:[]
}

export const tax = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLTAX:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATETAX:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATETAX:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETETAX:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}