import {ACTION_TYPES} from "../../actions/Store/stores";
const initialState = {
    list:[]
}

export const stores  = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLSTORES:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATESTORES:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATESTORES:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETESTORES:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}