import {ACTION_TYPES} from "../../actions/ExpenseTypes/expenseTypes";
const initialState = {
    list:[]
}

export const expenseTypes = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLEXPENSETYPE:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATEEXPENSETYPE:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATEEXPENSETYPE:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETEEXPENSETYPE:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}