    FETCH_ALLCREDITSALETRANSBYTRANSCODE:'FETCH_ALLCREDITSALETRANSBYTRANSCODE',
    FETCH_ALLCREDITSALETRANSBYDATEIN:'FETCH_ALLCREDITSALETRANSBYDATEIN',
    FETCH_ALLCREDITSALETRANSBYDATEINNUSERID:'FETCH_ALLCREDITSALETRANSBYDATEINNUSERID',
