    DELETEALLCREDITSALETRANS:'DELETEALLCREDITSALETRANS',
    
    FETCH_ALLCREDITSALETRANSDETAILSBYTRANSCODE: 'FETCH_ALLCREDITSALETRANSDETAILSBYTRANSCODE',
