//short version of fetch
export  const fetchAllTransactions = () =>dispatch =>{
    //get api req.
    api.creditSaleTransaction().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLCREDITSALETRANS,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}


export const createCreditSaleTransactionDetail =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
    // console.log(data)
     api.creditSaleTransaction().createcreditSaleTransDetail(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATECREDITSALETRANSDETAILS,
             payload: res.data
         })
         onSuccess();
         
     })
     .catch(err => console.log(err))
}

export const createCreditSaleTransaction =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
    // console.log(data)
     api.creditSaleTransaction().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATECREDITSALETRANS,
             payload: res.data
         })
         onSuccess();
         
     })
     .catch(err => console.log(err))
}

