export const updateCreditSaleTransaction =(transCode,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.creditSaleTransaction().update(transCode,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATECREDITTRANSATIONS,
             payload: {transCode, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

