import React, { lazy, useEffect, useState } from 'react'
import {
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout
} from '@coreui/react'
import CIcon from '@coreui/icons-react'

import Notificationss from 'src/views/Components/controls/Notification.js';
import NPopup from "../../Components/NPopup.js";

import { Notifications } from '@mui/icons-material';
import Card from '../../Components/Card/Card.js';
import "react-credit-cards/es/styles-compiled.css";
const Dashboard = (props) => {
  const [openNPopup, setOpenNPopup] = useState(false);
  // console.log(window.name);
  // console.log(window.userId);
  const [accountNumber, setAccountNumber] = useState("");
  const [accountName, setAccountName] = useState("");
  const [accBalance, setAccBalance] = useState(0);
  const [invLimit, setInvLimit] = useState(0);
  const [dateCreated, setDateCreated] = useState("");


  useEffect(() => {
    getAccount();
    // console.log(window.userId );
  }, [])


  const getAccount = async () => {

    try {
      const response = await fetch('http://risokaslick-001-site1.ktempurl.com/api/Accounts/get-acc-by-uid/' + window.userId, {
        // method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',

      })
      const content = await response.json();

      setAccountNumber(content.accNo);
      setAccBalance(content.balance);
      setInvLimit(content.investmentLimit);
      setAccountName(window.name);
      setDateCreated(content.dateCreated);

    } catch {

    }
  }

  return (
    <>

      <Card accountNumber={accountNumber} accountName={accountName} accbalance={accBalance} invLimit={invLimit} dateCreated={dateCreated} />

      <NPopup
        title="Stock Alert"
        openNPopup={openNPopup}
        setOpenNPopup={setOpenNPopup}
      >
        <Notificationss
          {...({ openNPopup, setOpenNPopup })}

        />
      </NPopup>


    </>
  );
}


export default Dashboard;
