import { ACTION_TYPES } from "../../actions/CreditSale/creditSale";
const initialState = {
    list: []
}

export const creditSale = (state = initialState, action) => {
    switch (action.type) {
        case  ACTION_TYPES.FETCH_ALLCREDITSALETRANS:
            return {
                ...state,
                list: [...action.payload]
            }

        case ACTION_TYPES.FETCH_ALLCREDITSALETRANSBYTRANSCODE:
            return {
                ...state,
                list: state.list.filter(x => x.transCode != action.payload)
            }

        case ACTION_TYPES.FETCH_ALLCREDITSALETRANSBYDATEIN:
            return {
                ...state,
                list: state.list.filter(x => x.dateIn != action.payload)
            }

        case ACTION_TYPES.FETCH_ALLCREDITSALETRANSBYDATEINNUSERID:
            return {
                ...state,
                list: state.list.filter(x => x.dateIn != action.payload && x.userId != action.payload)
            }

        case ACTION_TYPES.FETCH_ALLCREDITSALETRANSBYUSERID:
            return {
                ...state,
                list: state.list.filter(x => x.userId != action.payload)
            }

      
        case ACTION_TYPES.CREATECREDITSALETRANS:
            return {
                ...state,
                list: [...state.list, action.payload]
            }

       

        // case ACTION_TYPES.UPDATEPRODUCTS:
        //     return {
        //         ...state,
        //         list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
        //     }

        case ACTION_TYPES.UPDATECREDITTRANSATIONS:
            return {
                ...state,
                list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
            }

        case ACTION_TYPES.DELETEALLCREDITSALETRANS:
            return {
                ...state,
                list: state.list.filter(x => x.transCode != action.payload)
            }
        default:
            return state
    }
}