import { Paper, TableBody, TableCell, TableRow, TextField, Toolbar, withStyles, InputAdornment } from "@material-ui/core";
import React, { useState, useEffect, useRef } from "react";
//import * as actions from "../../../../../actions/Staff/addStaff";
import * as notificatonActions from "../../../../actions/Notifications/Notifications";
import { connect } from "react-redux";
import useTable from "../useTable";
import Controls from "../../controls/Controls";
import { EditOutlined, Search } from "@material-ui/icons";
import AddIcon from "@material-ui/icons/Add";
import PageHeader from "../../PageHeader";
import AddShoppingCartTwoToneIcon from '@material-ui/icons/AddShoppingCartTwoTone';
import Popup from "../../Popup";
// import AddNewProductForm from "../AddProduct/AddNewProductForm";

import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import CloseIcon from "@material-ui/icons/Close";
import DeleteIcon from "@material-ui/icons/DeleteForeverSharp";
import { useToasts } from "react-toast-notifications";
import Notification from "../../controls/Notification";
import ConfirmDialog from "../../controls/ConfirmDialog";
import Checkbox from '@mui/material/Checkbox';


const styles = theme => ({

    paper: {
        margin: theme.spacing(-8.9),
        padding: theme.spacing(6),

    },

    searchInput: {
        width: '75%'
    },

    newButton: {
        position: 'absolute',
        right: '10px '

    },

})

const headCells = [
    { id: 'nId', label: 'Notification Id' },
    { id: 'title', label: 'Title' },
    { id: 'message', label: 'Message' },
    { id: 'date', label: 'Date' },
    { id: 'Mark As Read', label: 'Mark As Read', disableSorting: true },
]

const Notifications = ({ classes, ...props }) => {

    const [filterFn, setFilterFn] = useState({ fn: items => { return items; } })
    const [openPopup, setOpenPopup] = useState(false)
    const [currentProductId, setcurrentProductId] = useState(0)
    const { addToast } = useToasts()
    const { notify, setNotify } = useState({ isOpen: false, message: '', type: '' })
    const [confirmDialog, setConfirmDialog] = useState(false)
    const [deleteRecord, setDeleteRecord] = useState(false)
    const [read, setRead] = useState(false);
    const [readNotification, setReadNotifications] = useState([]);
   
    const showToast = () => {
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        props.fetchAllProducts();
    }
    const onDelete = id => {
        props.deleteProduct(id, showToast)
        setConfirmDialog(false)

    }

    const Mark = (nId) => {
        setRead(!read);
        props.markRead(nId,{readBy:window.name});
        console.log(nId);
       
    }

    useEffect(() => {
        props. fetchUnRead();
       
       console.log(read);
       
    }, [read]);





    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting,
    } = useTable(props.notificationsList, headCells, filterFn)


    const handleSearch = e => {
        let target = e.target;
        setFilterFn({
            fn: items => {
                if (target.value == "")
                    return items;
                else {
                    return items.filter(x =>
                        x.nId.toLowerCase().includes(target.value.toLowerCase()) +
                        x.title.toLowerCase().includes(target.value.toLowerCase()) +
                        x.message.toLowerCase().includes(target.value.toLowerCase()) +
                        x.read.toLowerCase().includes(target.value.toLowerCase()) +
                        x.date.toLowerCase().includes(target.value.toLowerCase())) //+
                    //x.category.toLowerCase().includes(target.value.toLowerCase()))
                }




            }
        })
    }

    const addorEdit = (employee, resetForm) => {

        setOpenPopup(false)
        setNotify({
            isOpen: true,
            message: 'Submitted Successfully',
            type: 'Success'
        })
    }

    const openInPopup = record => {
        setcurrentProductId(record)
        setOpenPopup(true)
    }

    const openInDialog = () => {
        setConfirmDialog(true)
    }

    return (

        <>
            <PageHeader
                title="Stock Alerts"
                //subTitle="Form design with validation"
                icon={<AddShoppingCartTwoToneIcon fontSize="large" />}
            />,
            <Paper className={classes.paper} elevation={3}>

                <Toolbar>
                    <Controls.Input
                        className={classes.searchInput}
                        label="Search Notification"

                        InputProps={{
                            startAdornment: (<InputAdornment position="start">
                                <Search />
                            </InputAdornment>)
                        }}
                        onChange={handleSearch}

                    />
                    {/* <Controls.Button
                        text="Add New"
                        variant="outlined"
                        startIcon={<AddIcon />}
                        className={classes.newButton}
                        onClick={() => {setOpenPopup(true); setcurrentProductId(0);}}
                    /> */}
                </Toolbar>
                <table class="table .table-responsive{-sm|-md|-lg|-xl|-xxl} table-hover " >
                    <thead >
                    </thead>
                    <tbody>
                        <TblContainer >
                            <TblHead />
                            <TableBody>
                                {
                                    recordsAfterPagingAndSorting().map((record, index) => {
                                        return (<TableRow key={index} hover>
                                            <TableCell>{record.nId}</TableCell>
                                            <TableCell>{record.title}</TableCell>
                                            <TableCell>{record.message}</TableCell>

                                            <TableCell>{record.date} </TableCell>

                                            <TableCell>
                                                <Controls.ActionButton
                                                    color="primary"
                                                    // onClick={()=>{setCurrentId(record.id)}}
                                                    onClick={() => { Mark(record.nId) }}
                                                >
                                                    <EditOutlinedIcon fontSize="small" />
                                                </Controls.ActionButton>
                                            </TableCell>
                                        </TableRow>

                                        )
                                    })
                                }

                            </TableBody>
                        </TblContainer>


                    </tbody>
                </table>

                <TblPagination />
            </Paper>

            <Notification
                notify={notify}
                setNotify={setNotify}
            />
            <ConfirmDialog
                title="Are you sure you want to delete this record?"
                subTitle="This operation can't be reversed"
                confirmDialog={confirmDialog}
                setConfirmDialog={setConfirmDialog}
            />
        </>
        // {...({ currentProductId, setcurrentProductId }) }

    )
}

const mapStateToProps = state => ({
    notificationsList: state.notifications.list,

})

const mapActionToProps = {
    fetchAllNotifications: notificatonActions.fetchAllNotifications,
    // deleteProduct: notificatonActions.DeleteProduct,
    markRead: notificatonActions.updateNotification,
    fetchUnRead: notificatonActions.fetchAllUnReadNotifications,
}

export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(Notifications));