import React, { Component, useState, useEffect } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { HashRouter } from 'react-router-dom/cjs/react-router-dom.min';
import './scss/style.scss';
import Login from "./views/pages/login/Login";
import { accessToken,updateAccessToken } from './gVariables.js';
import Register from './views/pages/register/Register';

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

// Containers
const TheLayout = React.lazy(() => import('./containers/TheLayout'));

// Pages
// const Login = React.lazy(() => import('./views/pages/login/Login'));
// const Register = React.lazy(() => import('./views/pages/register/Register'));
const Page404 = React.lazy(() => import('./views/pages/page404/Page404'));
const Page500 = React.lazy(() => import('./views/pages/page500/Page500'));





// class App extends Component 

const App = ({ classes, ...props }) => {
  const [accessToken, setAccessToken] = useState(false);
  const [level, setLevel] = useState('');
  const [userId, setUserId] = useState(0);
  const [uName, setUName] = useState('');
  const [signup, setSignup] = useState(false);
  // window.accessToken = false;
  
  if (window.accessLevel === false) {
    // setAccessToken(false);
    // console.log("Logout" + window.logout);
  }
 
  useEffect(() => {
  
    // console.log(window.accessToken);
    getUser();
    // console.log("Logout" + window.logout);
  //  console.log("use effect in app.js " + window.accessToken)

  }, [ accessToken]);



  const getUser = async () => {
    try {
      const response = await fetch('http://risokaslick-001-site1.ktempurl.com/api/Auth/user/', {
        // method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',

      })
      const content = await response.json();
      setUName(content.fName + " " + content.sName);
      setUserId(content.id);
      // console.log(content.fName + " " + content.sName);
      window.name = content.fName + " " + content.sName;
      window.userId = content.userId;
      window.userLevelId = content.userLevelId;
      let uId = content.userLevelId;
      //  console.log(content);
      getUserLevel(uId);

    } catch {

    }
  }

  const getUserLevel = async (uId) => {
    try {
      const response = await fetch('http://risokaslick-001-site1.ktempurl.com/api/UserLevels/get-user-level-by-id/' + uId, {
        // method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',

      })
      const content = await response.json();

      window.accessLevel = content.level;
      setLevel(window.accessLevel);
      // console.log(window.accessLevel);

    } catch {

    }
  }


  // render() {

  if (accessToken === false || window.accessToken === false || window.accessToken === undefined) {
   
    if(signup === false){
      return (
        <div className="App">
          <Login {...({ accessToken, setAccessToken})} />
         
        </div>
  
      );

    }else {
      return (
        <div className="App">
          
          <Register {...({ signup, setSignup})}/>
        </div>
  
      );
    }
    

  } else {
    // console.log("global access token change " + window.accessToken);
    return (


      <HashRouter>
        <React.Suspense fallback={loading}>
          <Switch>
            <Route exact path="/register" name="Register Page" render={props => <Register />} />
            <Route exact path="/404" name="Page 404" render={props => <Page404 {...props} />} />
            <Route exact path="/500" name="Page 500" render={props => <Page500 {...props} />} />
            <Route path="/" name="Home" component={props => <TheLayout {...({uName, accessToken, setAccessToken,level}) } />} />
            {/* <Route  exact path="/login" name="Login Page" render={props => <Login {...props} />} /> */}
          </Switch>
        </React.Suspense>
      </HashRouter>
    );

  }


  // }
}

export default App;
