import {ACTION_TYPES} from "../../actions/StoreProducts/storeProducts";
const initialState = {
    list:[]
}

export const storeProducts  = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLSTOREPRODUCTS:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATESTOREPRODUCTS:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATESTOREPRODUCTS:
            return {
                ...state,
                list: state.list.map(x =>x.storeId == action.payload.storeId? action.payload:x)
            }

        case ACTION_TYPES.FETCHBYSTOREID:
            return {
                ...state,
                list: state.list.filter(x => x.storeId!= action.payload)
            }
        
        case ACTION_TYPES.FETCHBYSTOREIDNPRODUCTID:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        
        // case ACTION_TYPES.FETCHBYDATEINSTOREID:
        //     return {
        //         ...state,
        //         list: state.list.filter(x => x.id!= action.payload)
        //     }

        default:
            return state
    }
}