import {ACTION_TYPES} from "../../actions/Transactions/Transactions";
const initialState = {
    list:[]
}

export const transactions  = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLTRANSACTIONS:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATETRANSACTION:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.FETCHTRANSACTIONSBYACCNO:
            return {
                ...state,
                list: state.list.filter(x => x.accNo != action.payload)
            }
        case ACTION_TYPES.UPDATEACCOUNT:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            // case ACTION_TYPES.DELETEUSERLEVEL:
            // return {
            //     ...state,
            //     list: state.list.filter(x => x.id!= action.payload)
            // }
        default:
            return state
    }
}