import { ACTION_TYPES } from "../../actions/CashSale/cashSale";
const initialState = {
    list: []
}

export const cash = (state = initialState, action) => {
    switch (action.type) {
        

        case ACTION_TYPES.FETCH_ALLCASHSALETRANSDETAILS:
            return {
                ...state,
                list: [...action.payload]
            }

        case ACTION_TYPES.FETCH_ALLCASHSALETRANSDETAILSBYTRANSCODE:
            return {
                ...state,
                list: state.list.filter(x => x.transCode != action.payload)
            }

       
        case ACTION_TYPES.CREATECASHSALETRANSDETAILS:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        // case ACTION_TYPES.UPDATEPRODUCTS:
        //     return {
        //         ...state,
        //         list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
        //     }

        
        default:
            return state
    }
}