import api from "../api";
export const ACTION_TYPES = {
    CREATESUPPLIERS : 'CREATESUPPLIERS',
    UPDATESUPPLIERS: 'UPDATESUPPLIERS',
    DELETESUPPLIERS: 'DELETEPRODUCT',
    FETCH_ALLSUPPLIERS: 'FETCH_ALLSUPPLIERS'
}

