//short version of fetch
export  const fetchAllSupplier = () =>dispatch =>{
    //get api req.
    api.suppliers().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLSUPPLIERS,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createSupplier =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.suppliers().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATESUPPLIERS,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateSupplier =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.suppliers().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATESUPPLIERS,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteSupplier =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.suppliers().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETESUPPLIERS,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}