import api from "../api";
export const ACTION_TYPES = {
    CREATETAX : 'CREATETAX',
    UPDATETAX: 'UPDATETAX',
    DELETETAX: 'DELETETAX',
    FETCH_ALLTAX: 'FETCH_ALLTAX'
}

