//short version of fetch
export  const fetchAllTaxes= () =>dispatch =>{
    //get api req.
    api.tax().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLTAX,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createTax =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.tax().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATETAX,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateTax=(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.tax().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATETAX,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteTax =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.tax().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETETAX,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}