import api from "../api";
export const ACTION_TYPES = {
    CREATECASHSALETRANS:'CREATECASHSALETRANS',
    CREATECASHSALETRANSDETAILS:'CREATECASHSALETRANSDETAILS',
    FETCH_ALLCASHSALETRANS:'FETCH_ALLCASHSALETRANS',
    FETCH_ALLCASHSALETRANSBYTRANSCODE:'FETCH_ALLCASHSALETRANSBYTRANSCODE',
    FETCH_ALLCASHSALETRANSBYDATEIN:'FETCH_ALLCASHSALETRANSBYDATEIN',
    FETCH_ALLCASHSALETRANSBYDATEINNUSERID:'FETCH_ALLCASHSALETRANSBYDATEINNUSERID',
    FETCH_ALLCASHSALETRANSBYUSERID:'FETCH_ALLCASHSALETRANSBYUSERID',
    FETCH_ALLCASHSALETRANSDETAILS:'FETCH_ALLCASHSALETRANSDETAILS',
    DELETEALLCASHSALETRANS:'DELETEALLCASHSALETRANS',
    
    FETCH_ALLCASHSALETRANSDETAILSBYTRANSCODE: 'FETCH_ALLCASHSALETRANSDETAILSBYTRANSCODE',
    DELETEALLCASHSALETRANSDETAILS: 'DELETEALLCASHSALETRANSDETAILS',
    DELETEALLCASHSALETRANSDETAILSBYTRANSCODE: 'DELETEALLCASHSALETRANSDETAILSBYTRANSCODE',
    UPDATECASHTRANSATIONS:'UPDATECASHTRANSATIONS',
   
}

