//short version of fetch
export  const fetchAllTransactions = () =>dispatch =>{
    //get api req.
    api.cashSaleTransaction().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLCASHSALETRANS,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}


export const createCashSaleTransactionDetail =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
    // console.log(data)
     api.cashSaleTransaction().createCashSaleTransDetail(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATECASHSALETRANSDETAILS,
             payload: res.data
         })
         onSuccess();
         
     })
     .catch(err => console.log(err))
}

export const createCashSaleTransaction =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
    // console.log(data)
     api.cashSaleTransaction().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATECASHSALETRANS,
             payload: res.data
         })
         onSuccess();
         
     })
     .catch(err => console.log(err))
}

