export const updateCashSaleTransaction =(transCode,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.cashSaleTransaction().update(transCode,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATECASHTRANSATIONS,
             payload: {transCode, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteCashTransDetailByTransCode =(transCode, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.cashSaleTransaction().deleteByTranCode(transCode)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES. DELETEALLCASHSALETRANSDETAILSBYTRANSCODE,
             payload: transCode,
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

