import api from "../api";
export const ACTION_TYPES = {
    CREATENOTIFICATION : 'CREATENOTIFICATION',
    UPDATENOTIFICATION: 'UPDATENOTIFICATION',
    DELETENOTIFICATION: 'DELETENOTIFICATION',
    FETCH_ALLNOTIFICATIONS: 'FETCH_ALLNOTIFICATIONS',
    FETCH_ALLREADNOTIFICATIONS: 'FETCH_ALLREADNOTIFICATIONS',
    FETCH_ALLUNREADNOTIFICATIONS: 'FETCH_ALLUNREADNOTIFICATIONS'
}

