export const createNotification =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.notifications().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATENOTIFICATION ,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateNotification =(id,data) => dispatch =>{
    // data = formatData(data)
     api.notifications().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATENOTIFICATION,
             payload: {id, ...data}
         })
        //  onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteNotification =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.notifications().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETENOTIFICATION,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}