import {ACTION_TYPES} from "../../actions/Expenses/expenses";
const initialState = {
    list:[]
}

export const expenses = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLEXPENSES:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATEEXPENSES:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATEEXPENSES:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETEEXPENSES:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}