import {ACTION_TYPES} from "../../actions/UserLevel/userLevel";
const initialState = {
    list:[]
}

export const userLevel  = (state = initialState,action) =>{
    switch(action.type){
        case ACTION_TYPES.FETCH_ALLUSERLEVEL:
            return{
                ...state,
                list:[...action.payload]
            }
        case ACTION_TYPES.CREATEUSERLEVEL:
            return {
                ...state,
                list:[...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATEUSERLEVEL:
            return {
                ...state,
                list: state.list.map(x =>x.id == action.payload.id? action.payload:x)
            }

            case ACTION_TYPES.DELETEUSERLEVEL:
            return {
                ...state,
                list: state.list.filter(x => x.id!= action.payload)
            }
        default:
            return state
    }
}