import api from "../api";
export const ACTION_TYPES = {
    CREATESTOCKINS : 'CREATESTOCKINS',
    UPDATESTOCKINS: 'UPDATESTOCKINS',
    DELETESTOCKINS: 'DELETESTOCKINS',
    FETCH_ALLSTOCKINS: 'FETCH_ALLSTOCKINS',
    FETCHBYID:'FETCHBYID',
    FETCHBYDATEIN:'FETCHBYDATEIN',
    FETCHBYDATEINSTOREID:'FETCHBYDATEINSTOREID',

}

