//short version of fetch
export  const fetchAllStockIn = () =>dispatch =>{
    //get api req.
    api.stockIns().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLSTOCKINS,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createStockIn =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.stockIns().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATESTOCKINS,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateStockIn =(id, data,onSuccess) => dispatch =>{
    // data = formatData(data)
     api.stockIns().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATESTOCKINS,
             payload: {...id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const DeleteStockIn =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.stockIns().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETESTOCKINS,
             payload: {id}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const fetchById =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.stockIns().fetchById(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.FETCHBYID,
             payload: {id}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const fetchByDateIn =(dateIn, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.stockIns().fetchByDateIn(dateIn)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.FETCHBYNDATEIN,
             payload: {dateIn}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const fetchByDateInStoreId =(id,dateIn, storeId, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.stocksIn().fetchByDateInStoreId(id,dateIn, storeId)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.FETCHBYDATEINSTOREID,
             payload: {id,...dateIn, ...storeId}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}