import api from "../api";
export const ACTION_TYPES = {
    CREATEPRODUCTS : 'CREATEPRODUCTS',
    UPDATEPRODUCTS: 'UPDATEPRODUCTS',
    DELETEPRODUCTS: 'DELETEPRODUCT',
    FETCH_ALLPRODUCTS: 'FETCH_ALLPRODUCTS',
    UPDATESTOCKINHAND: 'UPDATESTOCKINHAND',

}

