//short version of fetch
export  const fetchAllProduct = () =>dispatch =>{
    //get api req.
    api.products().fetchAll()
        .then(response => {
            
            dispatch({
                type: ACTION_TYPES.FETCH_ALLPRODUCTS,
                payload: response.data
            })
        })
        .catch(err => console.log(err))
}

export const createProduct =(data, onSuccess) => dispatch =>{
    // data = formatData(data)
    // console.log(data);
     api.products().create(data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.CREATEPRODUCTS,
             payload: res.data
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateProduct =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.products().update(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATEPRODUCTS,
             payload: {id, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

export const updateStockInHand =(id,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.products().updateStockInHand(id,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATESTOCKINHAND,
             payload: {id, ...data},
           
         })
         onSuccess()
         console.log(data);
         
     })
     .catch(err => console.log(err))
}

export const DeleteProduct =(id, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.products().delete(id)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.DELETEPRODUCTS,
             payload: id
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}