import { ACTION_TYPES } from "../../actions/CashSale/cashSale";
const initialState = {
    list: []
}

export const cashSale = (state = initialState, action) => {
    switch (action.type) {
        case  ACTION_TYPES.FETCH_ALLCASHSALETRANS:
            return {
                ...state,
                list: [...action.payload]
            }

        case ACTION_TYPES.FETCH_ALLCASHSALETRANSBYTRANSCODE:
            return {
                ...state,
                list: state.list.filter(x => x.transCode != action.payload)
            }

        case ACTION_TYPES.FETCH_ALLCASHSALETRANSBYDATEIN:
            return {
                ...state,
                list: state.list.filter(x => x.dateIn != action.payload)
            }

        case ACTION_TYPES.FETCH_ALLCASHSALETRANSBYDATEINNUSERID:
            return {
                ...state,
                list: state.list.filter(x => x.dateIn != action.payload && x.userId != action.payload)
            }

        case ACTION_TYPES.FETCH_ALLCASHSALETRANSBYUSERID:
            return {
                ...state,
                list: state.list.filter(x => x.userId != action.payload)
            }

       

        case ACTION_TYPES.CREATECASHSALETRANS:
            return {
                ...state,
                list: [...state.list, action.payload]
            }

      

        // case ACTION_TYPES.UPDATEPRODUCTS:
        //     return {
        //         ...state,
        //         list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
        //     }

        case ACTION_TYPES.UPDATECASHTRANSATIONS:
            return {
                ...state,
                list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
            }

        case ACTION_TYPES.DELETEALLCASHSALETRANS:
            return {
                ...state,
                list: state.list.filter(x => x.transCode != action.payload)
            }
        default:
            return state
    }
}