import { ACTION_TYPES } from "../../actions/Products/products";
const initialState = {
    list: []
}

export const products = (state = initialState, action) => {
    switch (action.type) {
        case ACTION_TYPES.FETCH_ALLPRODUCTS:
            return {
                ...state,
                list: [...action.payload]
            }
        case ACTION_TYPES.CREATEPRODUCTS:
            return {
                ...state,
                list: [...state.list, action.payload]
            }

        case ACTION_TYPES.UPDATEPRODUCTS:
            return {
                ...state,
                list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
            }

        case ACTION_TYPES.UPDATESTOCKINHAND:
            return {
                ...state,
                list: state.list.map(x => x.id == action.payload.id ? action.payload : x)
            }

        case ACTION_TYPES.DELETEPRODUCTS:
            return {
                ...state,
                list: state.list.filter(x => x.id != action.payload)
            }
        default:
            return state
    }
}