import api from "../api";
export const ACTION_TYPES = {
    CREATECADETETRANS:'CREATECADETETRANS',
    CREATECADETETRANSDETAILS:'CREATECADETETRANSDETAILS',
    FETCH_ALLCADETETRANS:'FETCH_ALLCADETETRANS',
    FETCH_ALLCADETETRANSBYTRANSCODE:'FETCH_ALLCADETETRANSBYTRANSCODE',
    FETCH_ALLCADETETRANSBYDATEIN:'FETCH_ALLCADETETRANSBYDATEIN',
    FETCH_ALLCADETETRANSBYDATEINNUSERID:'FETCH_ALLCADETETRANSBYDATEINNUSERID',
    FETCH_ALLCADETETRANSBYUSERID:'FETCH_ALLCADETETRANSBYUSERID',
    FETCH_ALLCADETETRANSDETAILS:'FETCH_ALLCADETETRANSDETAILS',
    DELETEALLCADETETRANS:'DELETEALLCADETETRANS',
    
    FETCH_ALLCADETETRANSDETAILSBYTRANSCODE: 'FETCH_ALLCADETETRANSDETAILSBYTRANSCODE',
    DELETEALLCADETETRANSDETAILS: 'DELETEALLCADETETRANSDETAILS',
    DELETEALLCADETETRANSDETAILSBYTRANSCODE: 'DELETEALLCADETETRANSDETAILSBYTRANSCODE',
    UPDATECADETETRANSACTIONS:'UPDATECADETETRANSACTIONS',
    UPDATECADETETRANSACTIONSDETAILS:'UPDATECADETETRANSACTIONSDETAILS',
    TOPUPTRANS:'TOPUPTRANS',
    TOPUPTRANSDETAIL:'TOPUPTRANSDETAIL',
}

