export const updateCadeteTransaction =(transCode,data, onSuccess) => dispatch =>{
    // data = formatData(data)
     api.cadeteTransactions().update(transCode,data)
     .then(res =>{
         dispatch({
             type:ACTION_TYPES.UPDATECADETETRANSACTIONS,
             payload: {transCode, ...data}
         })
         onSuccess()
     })
     .catch(err => console.log(err))
}

