import React from 'react';
import { Dialog, DialogTitle, DialogContent, Typography, DialogActions, makeStyles, IconButton } from "@material-ui/core";
import Controls from "./Controls";
import NotListedLocation  from '@material-ui/icons/NotListedLocation';

const useStyles = makeStyles(theme => ({
    dialog: {
        padding: theme.spacing(2),
        position: 'absolute',
        top: theme.spacing(5),
    },

    dialogContent: {
        textAlign: 'center'
    },

    dialogTitle: {
        textAlign: 'center'
    },

    dialogAction: {
        justifyContent: 'center'
    },

    titleIcon:{
        backgroundColor: "#FFE3E6",
        color:theme.palette.secondary.main,
        '&:hover':{
            backgroundColor: "#FFE3E6",
            cursor:'default'
        },

        '& .MuiSvgIcon-root':{
            fontSize: '8rem',
        }
    }

}))


export default function ConfirmDialog(props) {
    const { title, subTitle, color, confirmDialog, setConfirmDialog, deleteRecord, setDeleteRecord } = props;
    const classes = useStyles()
    return (
        <Dialog open={confirmDialog} classes={{ paper: classes.dialog }}>
            <DialogTitle  className={classes.dialogTitle}>
                <IconButton disableRipple className= {classes.titleIcon}>
                   <NotListedLocation/>
                </IconButton>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Typography variant="h6">
                    {title}
                </Typography>
                <Typography variant="subtitle2">
                    {subTitle}
                </Typography>
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                <Controls.Button
                    text="No"
                    color="default"
                    onClick={()=> {setConfirmDialog(false)}}
                />
                <Controls.Button
                    text="Yes"
                    color="secondary"
                    onClick = {confirmDialog.onConfirm}
                />



            </DialogActions>

        </Dialog>
    )
}
